import { REMOVE_USER_BUSY_TIME } from '../../consts/usersReducer';
import deleteUserBusyTime from '../../db/queries/deleteUserBusyTime';

const removeUserBusyTimeActionCreator = busyTime => ({
  type: REMOVE_USER_BUSY_TIME,
  payload: busyTime,
});

const removeUserBusyTime = busyTime => (
  (dispatch) => {
    deleteUserBusyTime(busyTime);
    dispatch(removeUserBusyTimeActionCreator(busyTime));
  }
);

export default removeUserBusyTime;
