import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { injectIntl, intlShape } from 'react-intl';
import DialogWindow from '../../ui/DialogWindow';
import hellNo from '../../../images/hellNo.png';
import GreyBtn from '../../ui/GreyBtn';
import triggerModal from '../../../action-creators/pageReducer/triggerModal';
import { TRIGGER_DELETE_MODAL, TRIGGER_EDIT_MODAL } from '../../../consts/pageReducer';
import deleteEvent from '../../../action-creators/eventsReducer/deleteEvent';
import resetValues from '../../../action-creators/eventsReducer/resetValues';
import removeUserBusyTime from '../../../action-creators/usersReducer/removeUserBusyTime';
import './ShouldDeleteEvent.scss';
import { timeShape } from '../../../intl/shapes';

class ShouldDeleteEvent extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    triggerModal: PropTypes.func.isRequired,
    deleteEvent: PropTypes.func.isRequired,
    eventId: PropTypes.string.isRequired,
    resetValues: PropTypes.func.isRequired,
    currentTime: timeShape,
    removeUserBusyTime: PropTypes.func.isRequired,
  }

  handleDelete = () => {
    const {
      triggerModal,
      deleteEvent,
      eventId,
      resetValues,
      currentTime,
      removeUserBusyTime,
    } = this.props;

    deleteEvent(eventId);
    removeUserBusyTime(currentTime);
    triggerModal(TRIGGER_DELETE_MODAL);
    triggerModal(TRIGGER_EDIT_MODAL);
    resetValues();
  }

  render() {
    const { intl: { formatMessage }, triggerModal } = this.props;
    const text = formatMessage({ id: 'shouldDeleteEvent.text' });
    const picAlt = formatMessage({ id: 'shouldDeleteEvent.picAlt' });
    const cancelBtnText = formatMessage({ id: 'shouldDeleteEvent.cancelBtnText' });
    const deleteBtnText = formatMessage({ id: 'shouldDeleteEvent.deleteBtnText' });

    return (
      <DialogWindow
        text={text}
        picUrl={hellNo}
        picAlt={picAlt}
        className="should-delete-event-modal"
      >
        <GreyBtn
          onClick={() => {
            triggerModal(TRIGGER_DELETE_MODAL);
          }}
        >
          {cancelBtnText}
        </GreyBtn>

        <GreyBtn
          onClick={this.handleDelete}
          className="should-delete-event-modal__delete-btn"
        >
          {deleteBtnText}
        </GreyBtn>
      </DialogWindow>
    );
  }
}

const mapStateToProps = state => ({
  eventId: state.eventsReducer.currentEvent.id,
  currentTime: state.eventsReducer.currentEvent.time,
});

const mapDispatchToProps = {
  triggerModal,
  deleteEvent,
  resetValues,
  removeUserBusyTime,
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(ShouldDeleteEvent));
