import { DELETE_EVENT } from '../../consts/eventsReducer';
import deleteLocalStorageEvent from '../../db/queries/deleteLocalStorageEvent';

const deleteEventActionCreator = id => ({
  type: DELETE_EVENT,
  id,
});

const deleteEvent = id => (
  (dispatch) => {
    deleteLocalStorageEvent(id);
    dispatch(deleteEventActionCreator(id));
  }
);

export default deleteEvent;
