import getLocalStorageitems from './getLocalStorageItems';

const deleteUserBusyTime = (deletedEventbusyTime) => {
  getLocalStorageitems('users')
    .then(users => users.map(user => ({
      ...user,
      busyTime: user.busyTime.filter(busyTime => (
        busyTime.startingHours !== deletedEventbusyTime.startingHours
        && busyTime.startingMinutes !== deletedEventbusyTime.startingMinutes
        && busyTime.endingHours !== deletedEventbusyTime.endingHours
        && busyTime.endingMinutes !== deletedEventbusyTime.endingMinutes
      )),
    })))
    .then((updatedUsers) => {
      localStorage.setItem('users', JSON.stringify(updatedUsers));
    });
};

export default deleteUserBusyTime;
